export function useStatus(status: string): string | undefined {
    let uzStatus;
    switch (status) {
        case "done":
            uzStatus = "";
            break;
        case "pending":
            uzStatus = "";
            break;
        case "create":
            uzStatus = "";
            break;
        default:
            uzStatus = " ";
            break;
    }
    return uzStatus
}